    height: 100vh;
    display: flex;
    flex-direction: column;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-size: cover;
    font-family: Exo;
}

.header {
    display: grid;
    grid-template-columns: 15rem 1fr 9rem;
    align-self: center;
    align-items: center;
    height: 15%;
    width: 80%;
    background: white;
    background: linear-gradient(
